import React, {useContext, useState} from 'react';
import {Formik, Field, Form} from 'formik';
import {
    Button,
    LinearProgress,
} from '@material-ui/core';
import {TextField} from 'formik-material-ui';
import {FirebaseContext} from "../Firebase";
import Thumb from './Thumb';
import {useHistory, useLocation} from "react-router-dom";
import Typography from "@material-ui/core/Typography";
import axios from 'axios';
import Container from "@material-ui/core/Container";

const COLLECTION = 'campaigns';

async function saveCampaign(values, db, ownerID) {
    const {id} = values;
    const campaign = {ownerID, ...values};
    let doc = id ? db.collection(COLLECTION).doc(id) : db.collection(COLLECTION).doc();
    await doc.set(campaign);
    return doc.id;
}


export default function NewCampaign() {
    const firebase = useContext(FirebaseContext);
    const history = useHistory();
    const [files, setFiles] = useState([]);
    const data = new URLSearchParams(useLocation().search).get('data');
    let campaign = {
        title: '',
        text: '',
        contacts: [],
        state: 'draft',
        images: [],
    };
    if (data === 'updateCampaign') {
        campaign = JSON.parse(sessionStorage.getItem('campaign'));
    } else {
        campaign.contacts = JSON.parse(sessionStorage.getItem('newCampaign')) || [];
    }
    async function uploadAndGetFileURL(file) {
        const storageRef = firebase.getStorageRef();
        const ref = storageRef.child(file.name);
        const metadata = {
            contentType: file.type
        };
        const snapshot = await ref.put(file, metadata);
        return await snapshot.ref.getDownloadURL();
    }
    return (<Formik
        initialValues={campaign}
        validate={values => {
            const errors = {};
            if (!values.title) {
                errors.title = 'Required';
            }
            return errors;
        }}
        onSubmit={async(values, {setSubmitting}) => {
            values.images = await Promise.all(Array.from(files).map(uploadAndGetFileURL));
            console.log(values);
            const docID = await saveCampaign(values, firebase.db, firebase.getCurrentUser().uid);
            let error = false;
            try {
                await axios.post('https://rackley-beagle-8612.twil.io/newcampaign', {campaignID: docID});
            } catch (e) {
                error = e;
            }
            if (error) {
                alert('failed');
                console.error(error);
            } else {
                alert('Saved successfully!');
            }
            setTimeout(() => history.push('/contacts'), 5000);
            setSubmitting(false);
        }}
        render={({submitForm, isSubmitting,values, setFieldValue}) => (
            <Form>
                <Container maxWidth="md">
                    <Field
                        name="title"
                        type="text"
                        label="Title"
                        component={TextField}
                    />
                    <br/>
                    <Field
                        name="text"
                        type="text"
                        label="Text"
                        component={TextField}
                    />
                    <br/><br/>
                    <Typography variant="body2" >{`Sending to ${campaign.contacts.length} contact${campaign.contacts.length >1 ? 's':''}`}</Typography>
                    <br/>
                    <Typography variant="body1" >
                        Upload Images
                    </Typography>
                    <input aria-label="upload images" multiple name="uploader" type="file" accept="image/*" onChange={(event) => {
                        setFiles(event.currentTarget.files);
                        // setFieldValue("file", event.currentTarget.files[0]);
                    }} />
                    <br/>
                    {Array.from(files).map(file => {
                        return <Thumb key={file.name} file={file} />
                    })}
                    <br/>
                    {isSubmitting && <LinearProgress/>}
                    <br/>
                    <Button
                        variant="contained"
                        color="primary"
                        disabled={isSubmitting}
                        onClick={submitForm}
                    >
                        Submit
                    </Button>
                </Container>
            </Form>
        )}
    />)
};

