import React, {useContext} from 'react';
import ReactDOM from 'react-dom';
import CssBaseline from '@material-ui/core/CssBaseline';
import { ThemeProvider } from '@material-ui/core/styles';
import App from './App';
import theme from './theme';
import Firebase, { FirebaseContext } from './components/Firebase';
import CircularProgress from "@material-ui/core/CircularProgress";
import Container from "@material-ui/core/Container";

function NeedsFirebase(props) {
    const [isReady, setIsReady] = React.useState(false);
    const firebase = useContext(FirebaseContext);
    if (!firebase.isListening) {
        firebase.onAuthUserListener(() => setIsReady(true));
    }
    if (isReady) return props.component;
    return <Container disableGutters={true} maxWidth="md">
            <CircularProgress />
        </Container>;
}


ReactDOM.render(
    <ThemeProvider theme={theme}>
        {/* CssBaseline kickstart an elegant, consistent, and simple baseline to build upon. */}
        <CssBaseline />
        <FirebaseContext.Provider value={new Firebase()}>
            <NeedsFirebase component={<App />} />
        </FirebaseContext.Provider>,
    </ThemeProvider>,
    document.querySelector('#root'),
);
