import React, {useContext} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Checkbox from '@material-ui/core/Checkbox';
import Avatar from '@material-ui/core/Avatar';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/PersonAdd';
import DeleteIcon from '@material-ui/icons/Delete';
import SendIcon from '@material-ui/icons/Send';

import {FirebaseContext} from "./Firebase";
import {useHistory} from "react-router-dom";
import Container from "@material-ui/core/Container";
import Link from "@material-ui/core/Link";

const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
        backgroundColor: theme.palette.background.paper,
        minHeight: 500,
    },
}));

export default function CheckboxListSecondary() {
    const classes = useStyles();
    const history = useHistory();
    const [checkedContacts, setChecked] = React.useState([]);
    let [contacts, setContacts] = React.useState([]);
    const firebase = useContext(FirebaseContext);
    const db = firebase.db;
    const currentUser = firebase.getCurrentUser();
    if (!currentUser) history.push('/signIn');
    const ownerID = currentUser ? currentUser.uid : null;
    const fetchContacts = () => {
        db.collection('contacts').where('ownerID', '==', ownerID).get().then((snapshot) => {
            contacts = [];
            snapshot.forEach((doc) => {
                contacts.push({
                    id: doc.id,
                    ...doc.data(),
                })
            });
            sessionStorage.setItem('contacts', JSON.stringify(contacts));
            setContacts(contacts);
        });
    };
    if (!contacts.length && ownerID) {
        fetchContacts();
    }
    const handleToggle = value => () => {
        const currentIndex = checkedContacts.findIndex(contact => contact.id === value.id);
        if (currentIndex === -1) {
            checkedContacts.push(value);
        } else {
            checkedContacts.splice(currentIndex, 1);
        }
        value.checked = !value.checked;
        setChecked([...checkedContacts]);
    };
    const getAvatarLabel = (first, last) => {
        if (last.length) {
            return first[0] + last[0];
        }
        return first.substr(0,2);
    };
    const updateContact = (contact) => {
        const contactForUpdate = Object.assign(contact);
        delete contactForUpdate.checked;
        sessionStorage.setItem('contact', JSON.stringify(contactForUpdate));
        history.push('/contacts/new?data=updateContact');
    };
    const newCampaign = () => {
        sessionStorage.setItem('newCampaign', JSON.stringify(checkedContacts));
        history.push('/campaigns/new')
    };
    const deleteContacts = async () => {
        const promises = checkedContacts.map(async (contact) => {
            await db.collection('contacts').doc(contact.id).delete();
            handleToggle(contact);
        });
        await Promise.all(promises);
        fetchContacts();
    };
    const selectAll = () => {
        setContacts(contacts.map(contact => {contact.checked = true; return contact}));
        setChecked([...contacts]);
        alert('Selected all contacts');
    };
    return (
        <div>
            {checkedContacts.length > 0 && <Link onClick={selectAll}>Select all</Link>}
            <List className={classes.root}>
                {contacts.length > 0 && contacts.map(contact => {
                    const labelId = `checkbox-list-secondary-label-${contact.id}`;
                    return (
                        <ListItem key={contact.id} button onClick={() => updateContact(contact)}>
                            <ListItemAvatar>
                                <Avatar>{getAvatarLabel(contact.firstName, contact.lastName)}</Avatar>
                            </ListItemAvatar>
                            <ListItemText id={labelId} primary={`${contact.firstName} ${contact.lastName}`} />
                            <ListItemSecondaryAction>
                                <Checkbox
                                    edge="end"
                                    onChange={handleToggle(contact)}
                                    checked={contact.checked}
                                    inputProps={{ 'aria-labelledby': labelId }}
                                />
                            </ListItemSecondaryAction>
                        </ListItem>
                    );
                })}
            </List>
            <Container maxWidth="md">
                <Fab disabled={!checkedContacts.length} className={classes.fab} onClick={() => newCampaign()}>
                    <SendIcon/>
                </Fab>
                <Fab disabled={!ownerID} className={classes.fab} onClick={() => history.push('/contacts/new')}>
                    <AddIcon/>
                </Fab>
                <Fab disabled={!checkedContacts.length} className={classes.fab} onClick={() => deleteContacts()}>
                    <DeleteIcon/>
                </Fab>
            </Container>
        </div>
    );
}
