import React, {useContext} from 'react';
import {Formik, Field, Form} from 'formik';
import {
    Button,
    LinearProgress,
} from '@material-ui/core';
import {TextField} from 'formik-material-ui';
import MuiPhoneNumber from 'material-ui-phone-number';
import {FirebaseContext} from "./Firebase";
import {useHistory, useLocation} from "react-router-dom";
import Container from "@material-ui/core/Container";

async function saveContact(values, db, ownerID) {
    const {id} = values;
    const contact = {ownerID, ...values};
    let doc = id ? db.collection('contacts').doc(id) : db.collection('contacts').doc();
    await doc.set(contact);
}

export default function NewContact() {
    const firebase = useContext(FirebaseContext);
    const history = useHistory();
    const data = new URLSearchParams(useLocation().search).get('data');
    let contact = {
        firstName: '',
        lastName: '',
        phoneNumber: '',
    };
    if (data === 'updateContact') {
        contact = JSON.parse(sessionStorage.getItem('contact'));
    }
    const contacts = JSON.parse(sessionStorage.getItem('contacts'));
    return (<Formik
        initialValues={contact}
        validate={values => {
            const errors = {};
            if (!values.firstName) {
                errors.firstName = 'Required';
            }
            if (!values.phoneNumber) {
                errors.phoneNumber = 'Required';
            }
            return errors;
        }}
        onSubmit={async(values, {setSubmitting}) => {
            const duplicate = contacts.find((contact) => contact.phoneNumber === values.phoneNumber);
            if (duplicate) {
                alert(`Number already exists for ${duplicate.firstName} ${duplicate.lastName}`);
            } else {
                await saveContact(values, firebase.db, firebase.getCurrentUser().uid);
                history.push('/contacts');
                setSubmitting(false);
            }
        }}
        render={({submitForm, isSubmitting,values, setFieldValue}) => (
            <Form>
                <Container maxWidth="md">
                    <Field
                        name="firstName"
                        type="text"
                        label="First Name"
                        component={TextField}
                    />
                    <br/>
                    <Field
                        name="lastName"
                        type="text"
                        label="Last Name"
                        component={TextField}
                    />
                    <br/>
                    <Field
                        name="phoneNumber"
                        type="text"
                        label="Phone"
                        value={contact.phoneNumber}
                        component={MuiPhoneNumber}
                        defaultCountry='us' onlyCountries={['us']}
                        disableAreaCodes={true}
                        onChange={phone => {
                            setFieldValue('phoneNumber', phone.replace(/\(|\)|-|\s/g, ""));
                        }}
                    />
                    <br/>
                    {isSubmitting && <LinearProgress/>}
                    <br/>
                    <Button
                        variant="contained"
                        color="primary"
                        disabled={isSubmitting}
                        onClick={submitForm}
                    >
                        Submit
                    </Button>
                </Container>
            </Form>
        )}
    />)
};
